.card {
  display: flex;
  justify-content: center;
}
.card-info {
	width: 210px;
	height: 80px;
	background-image: -webkit-radial-gradient(right bottom, ellipse farthest-corner, #ADC0CE 0%, #E61F59 50%, #E4E4E4 100%);
	background-image: radial-gradient(ellipse farthest-corner at right bottom, #ADC0CE 0%, #E61F59 50%, #E4E4E4 100%);
	border-radius: 20px;
	border: 1px solid silver;
	position: relative;
}
.card-info-name,.card-info-downname,.card-info-data,.card-info-id,.card-info-avatar{
	display: block;
}
.card-info-name {
	color: #eeeeee;
	text-transform: uppercase;
	font-size: 0.9em;
}
.card-info-downname {
	color: silver;
	text-transform: uppercase;
	position: absolute;
	bottom: 10px;
}
.card-info-data {
	margin-top: 5px;
}
.card-info-avatar>img {
	width: 32px;
	height: 32px;
	position: absolute;
	right: 10px;
	top: 10px;
	border-radius: 100%;
}
